<template>
  <form
    id="signup"
    class="sign-up"
    autocomplete="on"
    v-if="!allwd">

    <h2 class="sign-up__title">Sign up</h2>

    <MyInput
      v-for="field in formFields"
      :key="field.label"
      :label="field.label"
      :name="field.name"
      :type="field.type || 'text'"
      :hint="field.hint"
      @blur="(val) => handleInput(field.name, val)"
      :class="[ `${field.name}-input`, { 'error' : $v[field.errClass].$error }]"
      :hasError="$v[field.errClass].$error"
      :error="$v[field.errClass]">
    </MyInput>

    {{ firstServerError }}

    <button class="sign-up__btn" @click.prevent="handleSignup">
      <p>Sign-up</p>
    </button>
  </form>
</template>

<script>
import MyInput from '../components/input.vue'
import { required, minLength } from 'vuelidate/lib/validators'
import UserAPI from '../services/api/userApi'
import { mapActions, mapGetters, mapState } from 'vuex'

export default {
  components: {
    MyInput
  },
  data() {
    return {
      firstName: '',
      lastName: '',
      email: '',
      username: '',
      password: '',
      formFields: [
        {
          name: 'firstName',
          label: 'First name',
          errClass: 'firstName'
        },
        {
          name: 'lastName',
          label: 'Last name',
          errClass: 'lastName'
        },
        {
          name: 'email',
          label: 'Email',
          errClass: 'email'
        },
        {
          name: 'username',
          label: 'Username',
          hint: 'Must be at least 6 characters and include 2 numbers',
          errClass: 'username'
        },
        {
          name: 'password',
          label: 'Password',
          hint: 'Password must contain 1 lower & uppercase letter, 1 number and be between 8-20 characters',
          errClass: 'password',
          type: 'password'
        }
      ]
    }
  },
  validations: {
    firstName: { required, minLength: minLength(2) },
    lastName: { required, minLength: minLength(2) },
    email: {
      required,
      email: (email) => {
        if (email === '') return true
        const regExp = /(^$|^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$)/g
        return regExp.test(email)
      }
    },
    username: {
      required,
      minLength: minLength(6),
      userNameFormat: (username) => {
        const reg = new RegExp(/[0-9]{2}/)
        return reg.test(username)
      },
      isUnique: async (username) => {
        if (username === '') return true
        const { data } = await UserAPI.isUserNameUnique(username)
        return data.unique
      }
    },
    password: {
      required,
      minLength: minLength(8),
      password: (password) => {
        if (password === '') return true
        const regExp = /((?=.*\d)(?=.*[a-z])(?=.*[A-Z]).{8,20})/g
        return regExp.test(password)
      }
    }
  },
  methods: {
    ...mapActions(['signup']),
    async handleSignup() {
      await this.$v.$touch()

      if (!this.$v.$error) {
        const userData = {
          firstName: this.firstName,
          lastName: this.lastName,
          username: this.username,
          email: this.email,
          password: this.password
        }

        this.signup(userData)
      }
    },
    handleInput(field, value) {
      this[field] = value
      this.$v[field].$touch()
    }
  },
  computed: {
    ...mapGetters([
      'firstServerError'
    ]),
    ...mapState([
      'allwd'
    ])
  }
}
</script>

